import { useState } from "react";

import {
  Collapse,
  Navbar,
  NavbarToggler,
  NavbarBrand,
  Nav,
  NavItem,
  NavLink
} from "reactstrap";

import styled from "styled-components";

const RMNavbarDiv = styled.div`
  height: 10vh;
  padding: 0;
  margin: 0;
  background: transparent;
  color: white;

  .fa.fa-navicon {
    color: white;
    font-size: 1.75em;
  }

  a {
    color: white;
  }

  .navbar {
    background: yellow;
  }

  .collapse {
    background: orange;
    text-align: center;
  }

  .nav-item {
    @media (max-width: 767px) {
      z-index: 9999;
      background: rgba(0, 0, 0, 0.5);
      height: 15vh;
    }
  }
`;

function RMNavbar() {
  const [toggle, setToggle] = useState(true);
  return (
    <RMNavbarDiv>
      <Navbar expand="md" sticky="top" className="justify-content-center">
        <NavbarToggler className="" onClick={() => setToggle(!toggle)}>
          <i className="fa fa-navicon" />
        </NavbarToggler>
        <Collapse isOpen={toggle} navbar>
          <Nav className="ml-auto" navbar>
            <NavbarBrand />
            <NavItem>
              <NavLink
                href={process.env.BACKEND_URL + "/"}
                className="nav-link"
              >
                Home
              </NavLink>
            </NavItem>
            <NavItem>
              <NavLink href={process.env.BACKEND_URL + "/about"}>About</NavLink>
            </NavItem>
            <NavItem>
              <NavLink href={process.env.BACKEND_URL + "/contactInfo"}>
                Contact
              </NavLink>
            </NavItem>
            <NavItem>
              <NavLink href={process.env.BACKEND_URL + "/revelerProcess"}>
                Reveler Process
              </NavLink>
            </NavItem>
            <NavItem>
              <NavLink href={process.env.BACKEND_URL + "/services"}>
                Services
              </NavLink>
            </NavItem>
          </Nav>
        </Collapse>
      </Navbar>
    </RMNavbarDiv>
  );
}

export default RMNavbar;
